#input {}

.ticket-add {
  align-items: center;
  background-color: #ffffff;
  border: 1px solid;
  border-color: #d9f0f4;
  border-radius: 32px;
  gap: 8px;
  justify-content: center;
  padding: 4px;
  position: relative;
  display: flex;
  outline: none;
}
.ticket-add:focus {
  outline: none;
}
.ticket-add .line-system-subtract-wrapper {
  align-items: flex-start;
  background-color: #f1fafb;
  border-radius: 24px;
  display: inline-flex;
  flex: 0 0 auto;
  gap: 8px;
  opacity: 0.5;
  padding: 8px;
  position: relative;
  outline: none;
}

.ticket-add .icon-instance-node {
  height: 20px !important;
  position: relative !important;
  width: 20px !important;
  
  
 
}

.ticket-add .link-9 {
  color: #2b333f;
  direction: rtl;
  font-family: "Yekan Bakh-Bold", Helvetica;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0;
  line-height: 28px;
  position: relative;
  text-align: center;
  width:  10px;
  /* width: 100%;  */
  padding: 6px;
  font-size: 16px;
  /* border: 1px solid #ccc; */
  border-radius: 4px;
  box-sizing: border-box;
  outline: none;
  /* border: 2px solid black; */
}

.ticket-add .fill-system-add-wrapper {
  align-items: flex-start;
  background-color: #f1fafb;
  border-radius: 24px;
  display: inline-flex;
  flex: 0 0 auto;
  gap: 10px;
  padding: 8px;
  position: relative;
  

}
.btn {
  border: none !important;
  background: none !important;
  cursor: pointer;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px 8px;
  cursor: pointer;
  margin: 0 4px;
  
}

.ticket-add.no-fill-button .fill-system-add-wrapper {
  display: none;
  outline: none;
  
}
