.panel-items {
  align-items: center;
  display: flex;
  position: relative;
  width: 217px;
}

.panel-items .btn{
  border: none;
  background: none;
}

.panel-items .instance-node-4 {
  height: 20px !important;
  position: relative !important;
  width: 20px !important;
}

.panel-items .frame-30 {
  align-items: center;
  display: inline-flex;
  flex: 0 0 auto;
  flex-direction: column;
  gap: 8px;
  justify-content: center;
  position: relative;
}

.panel-items .line-user-user-wrapper {
  align-items: center;
  background-color: var(--white);
  border-radius: 40px;
  display: inline-flex;
  flex: 0 0 auto;
  gap: 8px;
  justify-content: center;
  padding: 12px;
  position: relative;
}

.panel-items .text-wrapper-4 {
  color: var(--textdark-3);
  direction: rtl;
  font-family: "Yekan Bakh-Bold", Helvetica;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0;
  line-height: 22.4px;
  position: relative;
  white-space: nowrap;
  width: fit-content;
}

.panel-items .button-11 {
  align-items: center;
  border: 1px solid;
  border-color: var(--textdark-2);
  border-radius: 48px;
  display: inline-flex;
  flex: 0 0 auto;
  gap: 8px;
  justify-content: center;
  padding: 8px 16px;
  position: relative;
}

.panel-items .link-23 {
  color: var(--textdark-2);
  direction: rtl;
  font-family: "Yekan Bakh-Bold", Helvetica;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0;
  line-height: 17px;
  margin-top: -1px;
  position: relative;
  text-align: left;
  white-space: nowrap;
  width: fit-content;
}

.panel-items .frame-31 {
  align-items: center;
  display: inline-flex;
  flex: 0 0 auto;
  gap: 8px;
  position: relative;
}

.panel-items .text-wrapper-5 {
  color: var(--textdark-1);
  direction: rtl;
  font-family: "Yekan Bakh-Bold", Helvetica;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0;
  line-height: 22.4px;
  position: relative;
  white-space: nowrap;
  width: fit-content;
}

.panel-items .text-wrapper-6 {
  -webkit-background-clip: text !important;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(180deg, rgb(0, 56.1, 255) 0%, rgb(53.12, 109.65, 255) 100%);
  background-clip: text;
  color: transparent;
  direction: rtl;
  font-family: "Yekan Bakh-Bold", Helvetica;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0;
  line-height: 22.4px;
  position: relative;
  /* text-fill-color: transparent; */
  white-space: nowrap;
  width: fit-content;
}

.panel-items .line-user-user {
  height: 28px !important;
  position: relative !important;
  width: 28px !important;
}

.panel-items .line-finance-ticket-2-3 {
  height: 16px !important;
  position: relative !important;
  width: 16px !important;
}

.panel-items.property-1-big {
  background-color: var(--lightlight-blue);
  border-radius: 24px;
  flex-direction: column;
  gap: 24px;
  justify-content: center;
  overflow: hidden;
  padding: 20px 16px;
}

.panel-items.property-1-default {
  background-color: var(--lightlight-blue);
  border-radius: 40px;
  justify-content: space-between;
  padding: 12px 16px;
}

.panel-items.property-1-selected {
  background-color: var(--all-bg);
  border: 1px solid;
  border-color: transparent;
  border-image: linear-gradient(to bottom, rgb(0, 56.1, 255), rgb(53.12, 109.65, 255)) 1;
  border-radius: 40px;
  justify-content: space-between;
  padding: 12px 16px;
}
