#btn{
  align-items: center;
  border-radius: 48px;
  display: inline-flex;
  gap: 8px;
  justify-content: center;
  position: relative;
  border: none;
  cursor: pointer;
  background: none;
}

.button {
  align-items: center;
  border-radius: 48px;
  display: inline-flex;
  gap: 8px;
  justify-content: center;
  position: relative;
}

.button .link {
  direction: rtl;
  font-family: "Yekan Bakh-Bold", Helvetica;
  font-weight: 700;
  letter-spacing: 0;
  position: relative;
  text-align: left;
  white-space: nowrap;
  width: fit-content;
}

.button .line-finance-ticket {
  height: 20px !important;
  position: relative !important;
  width: 20px !important;
  /* display: none; */
}

.button.outline {
  border: 2px solid;
  border-color: #2b333f;
}
.otp-link{
  direction: rtl;
  font-family: "Yekan Bakh-Bold", Helvetica;
  font-weight: 700;
  letter-spacing: 0;
  position: relative;
  text-align: left;
  white-space: nowrap;
  width: fit-content;
  /* color: #fff; */
}
.otp-hover{
  color: #fff !important;
  background-color: #2b333f !important;
}

.button.fill {
  background: linear-gradient(180deg, rgb(0, 56.1, 255) 0%, rgb(53.12, 109.65, 255) 100%);}

.btn-hoverd{
  background: linear-gradient(184deg, rgba(0,56,255,1) 0%, rgba(53,110,255,0.41780462184873945) 100%) !important;

}

