.button.medium {
  padding: 8px 16px;
}

.button.big {
  padding: 12px 24px;
}

.button.outline .link {
  color: #2b333f;
  margin-top: -2px;
}

.button.fill .link {
  /* border: 2px solid black; */
  width: max-content;
  color: #ffffff;
  margin-top: -1px;
}
/* .button.fill .link:hover{
  color: blue;
} */

.button.medium .link {
  font-size: 14px;
  line-height: 20px;
}

.button.big .link {
  font-size: 18px;
  line-height: 24px;
}
