.bread-crumb {
  align-items: center;
  display: inline-flex;
  gap: 8px;
  justify-content: flex-end;
  position: relative;
  margin-left: 7vw;
  left: 0;
  right: 5vw !important;
}

.bread-crumb .link-13 {
  color: #2b333f;
  direction: rtl;
  font-family: "Yekan Bakh-Medium", Helvetica;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: normal;
  margin-top: -1px;
  position: relative;
  text-align: left;
  width: max-content;
  
}

.bread-crumb .line-system-arrow {
  height: 12px !important;
  position: relative !important;
  width: 12px !important;
}
