.today {
  align-items: center;
  background-color: #ffffff;
  border: 1px solid;
  border-color: #d9f0f4;
  border-radius: 32px;
  gap: 4px;
  height: 40px;
  padding: 8px 12px;
  position: relative;
}

.today .frame-8 {
  position: relative;
}

.today .link-10 {
  color: #2b333f;
  direction: rtl;
  font-family: "Yekan Bakh-Medium", Helvetica;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 24px;
  margin-top: -1px;
  position: relative;
  text-align: center;
  white-space: nowrap;
  width: fit-content;
}

.today .line-business {
  height: 20px !important;
  position: relative !important;
  width: 20px !important;
}

.today.date-today {
  display: flex;
  width: 96px;
}

.today.date-calendar {
  display: inline-flex;
  justify-content: center;
}

.today.date-tomorrow {
  display: inline-flex;
  justify-content: center;
}

.today.date-today .frame-8 {
  align-items: center;
  display: inline-flex;
  flex: 0 0 auto;
  gap: 4px;
  padding: 0px 10px;
}

.today.date-calendar .frame-8 {
  color: #2b333f;
  direction: rtl;
  font-family: "Yekan Bakh-Medium", Helvetica;
  font-size: 16px;
  font-weight: 500;
  height: 22px;
  letter-spacing: 0;
  line-height: 24px;
  text-align: center;
  white-space: nowrap;
  width: 48px;
}

.today.date-tomorrow .frame-8 {
  color: #2b333f;
  direction: rtl;
  font-family: "Yekan Bakh-Medium", Helvetica;
  font-size: 16px;
  font-weight: 500;
  height: 22px;
  letter-spacing: 0;
  line-height: 24px;
  text-align: center;
  white-space: nowrap;
  width: 48px;
}

.icon{
  width: 20px;
  height: 20px;
  
}