.frame {
  align-items: center;
  border-radius: 40px;
  display: inline-flex;
  gap: 4px;
  padding: 4px 12px;
  position: relative;
}

.frame .div-2 {
  direction: rtl;
  font-family: "Yekan Bakh-Bold", Helvetica;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0;
  line-height: normal;
  margin-top: -1px;
  position: relative;
  text-align: center;
  width: fit-content;
}

.frame .line-system-check {
  height: 14px !important;
  position: relative !important;
  width: 14px !important;
}

.frame.waiting {
  background: linear-gradient(180deg, rgba(0, 56.1, 255, 0.1) 0%, rgba(53.12, 109.65, 255, 0.1) 100%);
}

.frame.closed {
  background-color: var(--all-bg);
}

.frame.open {
  background-color: #ffa6201a;
}

.frame.waiting .div-2 {
  -webkit-background-clip: text !important;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(180deg, rgb(0, 56.1, 255) 0%, rgb(53.12, 109.65, 255) 100%);
  background-clip: text;
  color: transparent;
  text-fill-color: transparent;
}

.frame.closed .div-2 {
  color: var(--text-secondary);
}

.frame.open .div-2 {
  color: var(--orange);
}
