body{
  font-family: "Yekan Bakh-Medium";
}
.input {
  font-family: "Yekan Bakh-Medium";
  align-items: center;
  background-color: #ffffff;
  border: 1px solid;
  border-color: #d9f0f4;
  border-radius: 32px;
  display: flex;
  gap: 8px;
  justify-content: center;
  padding: 8px;
  position: relative;
  width: 416px;
}

.input :focus{
  outline: none;
  font-family: "Yekan Bakh-Medium";

}

.input .link-11 {
 
  color: #2b333f;
  direction: rtl;
  font-family: "Yekan Bakh-Bold", Helvetica;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0;
  line-height: 28px;
  margin-top: -1px;
  position: relative;
  text-align: center;
  white-space: nowrap;
  width: fit-content;
}
.input-field {
  
  font-family: "Yekan Bakh-Medium";
  width: 100%;
  padding: 6px;
  font-size: 16px;
  /* border: 1px solid #ccc; */
  border-radius: 4px;
  box-sizing: border-box;
  border: none;
  align-items: center;
  align-self: center;
  text-align: end;
  /* background-color: #2b11333f; */
}
.input ::placeholder{
  opacity: 0.6;
}

.input-field :focus{
  outline: none;
}
.input-field ::selection{
  border: none;
}