.waved-title {
  align-items: center;
  display: inline-flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
}

.waved-title .p {
  color: var(--textdark-3);
  direction: rtl;
  font-family: "Yekan Bakh-Heavy", Helvetica;
  font-weight: 400;
  letter-spacing: 0;
  line-height: normal;
  margin-top: -1px;
  position: relative;
  text-align: center;
  width: fit-content;
}

.waved-title .frame-7 {
  align-self: stretch;
  height: 16px;
  position: relative;
  width: 100%;
  z-index: 0;
}

.waved-title .property-1-default {
  font-size: 36px;
}

.waved-title .property-1-mobile {
  font-size: 24px;
}

.waved-title .property-1-0-default {
  margin-top: -16px;
}

.waved-title .property-1-0-mobile {
  margin-top: -14px;
}

@media only screen and (max-width: 768px) {
  .waved-title .property-1-0-mobile {
    width: 150px;
    margin-top: 0px;
  }
}