.property-web-wrapper {
  align-items: center;
  display: inline-flex;
  flex-direction: column;
  position: relative;
  font-family: Yekan Bakh;
}

.property-web-wrapper .link-12 {
  font-weight: 900;

  color: #141619;
  direction: rtl;
  margin-top: -1px;
  position: relative;
  text-align: center;
  width: fit-content;
  z-index: 1;
}

.property-web-wrapper .frame-9 {
  align-self: stretch;
  height: 16px;
  margin-top: -12px;
  position: relative;
  width: 100%;
  z-index: 0;
}

.property-web-wrapper .web {
  font-family: Yekan Bakh !important;
  font-weight: 900 !important;
  font-family: var(--36-bold-font-family);
  font-size: var(--36-bold-font-size);
  font-style: var(--36-bold-font-style);
  font-weight: var(--36-bold-font-weight);
  letter-spacing: var(--36-bold-letter-spacing);
  line-height: var(--36-bold-line-height);
}

.property-web-wrapper .mobile {
  font-family: "Yekan Bakh-Heavy", Helvetica;
  font-size: 24px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: normal;
}
