.go-to-up {
  align-items: flex-start;
  border-radius: 40px;
  display: inline-flex;
  gap: 10px;
  padding: 12px;
  position: relative;
}

.go-to-up .class {
  height: 20px !important;
  position: relative !important;
  width: 20px !important;
}

.go-to-up .class-2 {
  height: 24px !important;
  position: relative !important;
  width: 24px !important;
}

.go-to-up.blue-mob {
  background: linear-gradient(180deg, rgb(0, 56.1, 255) 0%, rgb(53.12, 109.65, 255) 100%);
}

.go-to-up.yellow {
  background-color: var(--orange);
}

.go-to-up.blue {
  background: linear-gradient(180deg, rgb(0, 56.1, 255) 0%, rgb(53.12, 109.65, 255) 100%);
}
